import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import About from './About.js';
import ProjectList from './ProjectList.js';
import Footer from './Footer.js';
import CssBaseline from '@material-ui/core/CssBaseline';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import { withTheme } from '@material-ui/core/styles';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#0D47A1',
    },
    secondary: {
      main: '#880e4f',
    },
  },
});

const heartStyle = {
  position: 'relative',
  top: '6px'
};

class App extends Component {
  componentDidMount(){
    document.title = "Conlon Novak - Portfolio"
  }
  render() {
    return (
      <MuiThemeProvider theme={theme}>
        <React.Fragment>
          <CssBaseline />
          <div className="App">
            <About/>
            <ProjectList />
            <Footer/>
            <div className="copyright">
              <p>&#169; {(new Date()).getFullYear()} <a href="mailto:conlonnovak+website@cmu.edu">Conlon Novak</a>. All rights reserved. Made with <FavoriteBorder color="secondary" style={heartStyle}/> using <a href="https://reactjs.org/">React</a> and <a href="https://material-ui.com/">Material-UI</a>.</p> 
            </div>
          </div>
      </React.Fragment>
    </MuiThemeProvider>
    );
  }
}

export default withTheme(theme)(App);