import React from 'react';
import * as path from "path";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Chip from '@material-ui/core/Chip';
import AwardsPopoverButton from './AwardsPopoverButton.js';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import classNames from 'classnames';


const projectDir = path.join(process.env.PUBLIC_URL, "projects");
const styles = theme => ({
  root: {
    maxWidth: 660,
    margin: 'auto',
    marginBottom: 10,
    marginTop: 10,
  },
  heading: {
    fontSize: theme.typography.pxToRem(20),
    flexBasis: '33.33%',
    flexShrink: 0,
    display: 'flex', 
    justifyContent: 'left', 
    alignItems: 'center',
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
    justifyContent: 'center',
    alignItems: 'center',
    margin: 'auto',

  },
  chip: {
    padding: theme.spacing.unit / 2,
    margin: theme.spacing.unit / 2,
  },
  content: {
    display: 'flex', 
    textAlign: 'left',
    alignItems: 'center',
    clear:'both',
    paddingBottom: 8,    
  },
  links:{
    display: 'flex', 
    textAlign: 'left',
    alignItems: 'center',
    clear:'both',
    paddingLeft: 8,
    paddingBottom: 18,
  }
});

class ProjectPanel extends React.Component {
  state = {
    expanded: null,
    panels: [],
  };

  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };

  loadDesc() {
    try {
      this.props.projects.json()
      .then(jsn => {
        this.setState({ projects: jsn.projects });
      });
    }
    catch(err) {
      console.log(err.message);
      console.log("Failed to load other projects.");
    }
  }

  //TO-DO Refactor
  generateLinks(j){
    const { classes } = this.props;
    let elements = []
    if (this.props.projects[j].links != null) {
      elements.push(<Button disabled className={classes.button}>Links</Button>);
      for (let i=0; i<this.props.projects[j].links.length; i++){
        if (this.props.projects[j].links[i].name === "Website"){
          elements.push(<IconButton className={classNames(classes.icon, 'fa fa-globe-americas fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "Github"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fab fa-github fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "Devpost"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fab fa-dev fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "Dribbble"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fas fa-basketball-ball fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "News"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fas fa-newspaper fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "Game"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fas fa-gamepad fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "Publication"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fas fa-book fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else if (this.props.projects[j].links[i].name === "Link"){
          elements.push(<IconButton className={classNames(this.props.icon, 'fa fa-globe-americas fa-inverse fa-lg')} color="primary" href={this.props.projects[j].links[i].link}/>);
        }
        else{
        elements.push(<Button color="primary" href={this.props.projects[j].links[i].link}>{this.props.projects[j].links[i].name}</Button>);
        }
      }
    }
    elements.push(this.generateAwards(j));
    return(elements); 
  }

  generateAwards(i){//className={classes.margin}>
    let elements = []
    const { classes } = this.props;
    if (this.props.projects[i].awards != null && this.props.projects[i].awards.length >= 1) {
      elements.push(<Button disabled className={classes.button}>Awards</Button>);
      elements.push(<AwardsPopoverButton awards = {this.props.projects[i].awards}/>);
      return(elements); 
    }
  }

  componentDidMount() {
    this.loadDesc();
  }

  render() {
    const { classes } = this.props;
    const { expanded } = this.state;
    let i = this.props.project
    if (this.props.projects != null){
      return(
        <ExpansionPanel expanded={this.state.expanded === 'panel'+i} onChange={this.handleChange('panel'+i)}>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>{this.props.projects[i-1].title}</Typography>
            <Chip label={this.props.projects[i-1].type} className={classes.chip} color="secondary"/>
            <Typography className={classes.secondaryHeading}>{this.props.projects[i-1].tagline}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails className={classes.content}>
            <Typography>
              {this.props.projects[i-1].desc}
            </Typography>
          </ExpansionPanelDetails>
          <div className={classes.links}>
            {this.generateLinks(i-1)}
          </div>
        </ExpansionPanel>
      );
    }
    else{ 
      return(null);
    }
  }
}

ProjectPanel.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ProjectPanel);