import React from 'react';
import * as path from "path";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Chip from '@material-ui/core/Chip';
import AwardsPopoverButton from './AwardsPopoverButton.js';
import Button from '@material-ui/core/Button';
import ProjectPanel from './ProjectPanel.js';


const projectDir = path.join(process.env.PUBLIC_URL, "projects");
const styles = theme => ({
  root: {
    minWidth: 660,
    width: '50%',
    margin: 'auto',
    marginBottom: 10,
    marginTop: 10,
  }
});

class ProjectAccordion extends React.Component {
  state = {
    panels: [],
  };

  componentDidMount() {
    this.loadDesc();
  }

  loadDesc() {
    try {
      let jsonPath = path.join(projectDir, this.props.name + ".json");
      console.log("Fetching information for project" + this.props.name + " from " + jsonPath);
      fetch(jsonPath)
      .then(response => {
        return response.json();
      })
      .then(jsn => {
        this.setState({ projects: jsn.projects });
      });
    }
    catch(err) {
      console.log(err.message);
      console.log("Failed to load other projects.");
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <ProjectPanel project={1} projects={this.state.projects}/>
        <ProjectPanel project={2} projects={this.state.projects}/>
        <ProjectPanel project={3} projects={this.state.projects}/>
        <ProjectPanel project={4} projects={this.state.projects}/>
      </div>
    );
  }
}

ProjectAccordion.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ProjectAccordion);